@font-face {
  font-family: 'clock';
  src: url('font/alarmClock.ttf');
}

body {
  font-family: 'clock';
  text-transform: uppercase;
  background-color: black;
  color: #02fc0a;
}

#root {
  display: grid;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  justify-items: center;
  align-items: center;
}

.board {
  border: 3px solid pink;
  position: absolute;
  display: grid;
  grid-template-columns: 3fr 1fr;
  height: 50%;
  width: 80%;
}

#keyboard-area {
  display: grid;
  grid-template-rows: 1fr 3fr;
}

#keyboard-area > p {
  justify-self: center;
  font-size: 2em;
}

.panel {
  border: red 1px solid;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.pad {
  transition: 900ms;
  align-self: center;
  text-align: center;
  font-size: 5em;
  text-transform: uppercase;
  cursor: pointer;
}

.pad:hover {
  transition: 400ms;
  background-color: blue;
}

#volume-controllers {
  display: grid;
  grid-template-rows: 1fr 4fr;
  justify-content: center;
  font-size: 1.3em;
  text-align: center;
}

.switch {
  font-family: 'clock' !important;
}

.slider {
  border: 2px solid red;
  justify-self: center;
}

.MuiSlider-thumb {
  background-color: gray !important;
  border-radius: unset !important;
  height: 23px !important;
  margin-top: 3px !important;
}

.MuiSlider-rail {
  height: 100% !important;
  color: blue;
}

.MuiSlider-track {
  color: green;
  width: 100% !important;
  left: 0px;
}